<template>
  <div class="sidebar">
    <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" theme="dark" unique-opened router background-color="#545c64"
             text-color="#fff"
             active-text-color="#ffd04b">
      <template v-for="item in siderbarItems">
        <template v-if="item.subs">
          <el-submenu :index="item.index">
            <template slot="title"><i :class="item.icon"></i>{{ item.title }}</template>
            <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index">{{ subItem.title }}</el-menu-item>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index">
            <i :class="item.icon"></i>{{ item.title }}
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
  import store from '../store'
export default {
  name: '',
  data () {
    return {
      defaultActive:'',
      cerList:[],
    }
  },
  mounted(){
    store.dispatch('loadSiderbarData');
  },
  computed:{
    siderbarItems(){
      var siderbarItems = store.state.siderbarItems;
      //this.defaultActive = siderbarItems[1].subs[0].index
      return siderbarItems;
    },
    onRoutes(){
      return this.$route.path.replace('/','');
    }
  },
  created: function(){
    this.getCerList();
  },
  methods:{
    getCerList: function(){
      let self = this;
      store.dispatch('loadSiderbarData')//使用store.dispatch( 'loadSiderbarData' )来触发action中的 loadSiderbarData 方法

    },
  }
}
</script>
<style scoped>
  .sidebar{
    display: block;
    position: absolute;
    width: 250px;
    left: 0;
    top: 80px;
    bottom:0;
    background: rgb(84, 92, 100);
    padding:30px 0;
    text-align: left;
  }
  .sidebar > ul {
    width:100%;
    height:100%;
  }
  .sidebar>>>.el-menu{
    border:none
  }
</style>



