<template>
  <div class="">
    <v-head></v-head>
    <v-sidebar></v-sidebar>
    <div class="content">
      <transition name="move" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
  </div>
</template>

<script>
  import vHead from '../components/Header'
  import vSidebar from '../components/Sidebar'
export default {
  name: 'Home',
  components: {
    vHead,vSidebar
  },
  data () {
    return {

    }
  }
}
</script>
<style scoped>
.content{
  background: none repeat scroll 0 0 #fff;
  position: absolute;
  left: 250px;
  right: 0;
  top: 80px;
  bottom: 0;
  width: auto;
  padding: 30px;
  box-sizing: border-box;
  overflow-y: scroll;
  z-index:1;
}
</style>



